.categoryContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin-top: 40px;
}

.borderSolidLine {
  margin-top: 33px;
  border-bottom: 0.5px solid #808080;
  opacity: 0.6;
  width: 100%;
}

.categoryView {
  margin: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.categoryView img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
}

.categoryItem {
  margin-right: 40px;
  margin-top: 24px;
  flex-basis: 10%;
}

.categoryItem a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.categoryItem > h2 {
  font-size: 24px;
}

.FOOTER {
  margin-top: 150px;
}

.arrow-back {
  font-size: 28px;
  margin-right: 1rem;
}
